.ContentPage {
  padding-top: 0;
  padding-bottom: 0;
}
.ContentPage-inner {
  overflow-y: hidden;
  padding-top: 0;
  padding-bottom: 0;
}

.slide-container {
  padding-bottom: 60px;
}

.react-slideshow-wrapper,
.react-slideshow-wrapper div,
.react-slideshow-fade-wrapper,
.react-slideshow-fade-wrapper div {
  height: 100%;
}
.slide-container > div,
.react-slideshow-container {
  height: calc(100% - 10px);
}

.react-slideshow-fade-wrapper  .each-slide > div,
.react-slideshow-wrapper .each-slide > div {
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

.each-slide:hover .each-slide-text {
  opacity: 1;
  transition: opacity 0.4s;
}
.each-slide-text {
  opacity: 0;
  transition: opacity 0.6s 0.4s;
  margin-top: 10px;
  padding: 10px;
  background: rgb(245 245 245);
  display: inline-block;
}
.each-slide-text a {
  display: inline-block;
  text-decoration: none;
  color: #009fe3;
}
.each-slide-text a:hover {
  text-decoration: underline;
}
